import React, { Component, useState, useEffect } from 'react';
import './App.css';
import cloneDeep from 'lodash/cloneDeep';

export default () => {
    const [task, setTask] = useState("");
    const [arr, setArr] = useState([]);
    var cloneArr;

    const handleSubmit = (event) => {
        event.preventDefault();
        setArr([...arr, event.target.value]);
    }

    const deleteRow = (data) => {
        var conf = window.confirm("Delete" + data + "from to-do list?");

        if(conf) {
            cloneArr = cloneDeep(arr);
            setArr(cloneArr.splice(indexOf(data), 1));
        }
    }
    
    var content = arr.map(
        (item) => 
        <div className = "rows" name = {item} key = {item}>
            {item}

            <button onClick = {deleteRow}> Del </button>
        </div> 
    );

    return (
        <div className = "App">
            <div className = "content">
                <form onSubmit = {handleSubmit}>
                    <label>
                        Task:
                        <input 
                            type = "text"
                            autoFocus
                            onFocus = {e => target.value !== '' ? e.target.value = '' : ''}
                            placeholder = "Enter Text Here"
                        />
                    </label>
                    <input type = "submit"/>
                </form>
            </div> 

            <div className = "taskList">
                {content}
            </div>
        </div>
    );
};

